.Content {
    display: grid;
    width: 100%;
    grid-area: data;
    }
.imageBlock {
    display: block;
    float: left;
    widht: 80px;
    height: 700px;

}
.fullName {
    display: block;
    float: left;
    height: 25px;
    margin-top: 25px;
    padding-left: 25px;

}
.statusBlock{
    height: 25px;
    display: block;
    margin-top: 60px;
    cursor: pointer;
    padding-left: 105px;
}
.profileInfo {
    display: block;
    margin-top: 10px;
    padding-left: 105px;
}
.profileInfo div {
    padding-bottom: 10px;
    height: auto;

}

.profileImg {
    margin-top: 25px;
    widht: 50px;
    height: 80px;
    border-radius: 50px;
}
.logo {

}
.button a{

}
.contact {
    padding-left: 10px;
    padding-top: 5px;
}

.Error {
    border-style: dashed;
    color: Black;
}
.profileForm {
    margin-right: 20px;
    padding: 20px;
    margin-top: 10px;
    display: grid;
    grid-template-columns: 2fr 5fr;
    grid-auto-rows: 3em;
    grid-gap: 0.5em;
}


.profileFormButton {
    height: 35px;
    margin-left: 260px;
}
