.NavBar {
    grid-area: navbar;
    width: 100%;
    }

.NavBar a {
    display: block;
    font-family: Calibri, Candara, Segoe, "Segoe UI", Optima, Arial, sans-serif;
    font-size: 18px;
    color: black;
    height: 25px;
    widht: 100%;
    margin: 5px 0 0 20px;
    text-decoration: none;
    white-space: nowrap;
}
  
.NavBar a:hover {
  font-weight: bold;
  font-size: 20px;
  }

.Friends {
    color: black;
    padding-top: 5px;
    text-align: left;
    margin: 5px auto;
    width: 100%;
    height: 30px; /* Размеры */

}

.Friends h4{
    padding-left: 25px;
    text-align: left;
    color: black;
}
.NavBar .activeLink {
    font-weight: bold;
    font-size: 21px;
}
.editMode span{
    display: block;
    width: 100%;
    cursor: pointer;
}