.app-wrapper {
    width: 100%;
    max-width: 800px;
    min-width: 650px;
    margin:0 auto;
    background-color: #F0EBEB;
    display: grid;
    grid-template-areas:
    "header header"
    "navbar data";
    grid-template-rows: 50px 700px;
    grid-template-columns: 1fr 3fr;
}
.active {
    color: blue;
}
   