.Header {
    grid-area: header;
    width: 100%;
    height: 100%;
    margin-left: auto;
    background-color: #FFFFFF;
    align-self: center;
    }
    
.Header img{
    padding-left: 25px;
    width: 50px;
    height: 50px;
    }

.loginBlock {
    float: right;
    height: 100%;
    padding-top: 5px;
}

.loginBlock span, a{
    padding-right: 20px;
}

.loginBlock button, a {
    color: black;
    background-color: #F0EBEB;
    height: 40px;
    text-decoration: none;
    border: none;
}

.loginBlock button:hover {
    background-color: #FFFFFF;
    text-decoration: none;
    border: none;
}
.loginBlock button:active {
    background-color: #FFFFFF;
    text-decoration: none;
    font-weight: bold;
    border: none;
}